.panel-select-button {
	position: absolute;
	top: calc(100% + 12px);
	left: 0;
	z-index: 10;
	width: 100%;
	display: flex;
	flex-direction: column;
	padding: rem(10);
	border: 1px solid #e0e0e0;
	box-shadow: 0px 4px 5px 0px rgba(0, 0, 0, 0.08);
	background-color: white;
	border-radius: rem(5);

	&::before {
		content: "";
		position: absolute;
		top: -2px;
		left: 50%;
		z-index: -1;
		transform: rotate(45deg) translateX(-50%);
		width: 10px;
		height: 10px;
		display: inline-block;
		border: 1px solid transparent;
		background-color: white;
    	border-color: #e0e0e0;
		box-shadow: -2px -2px 6px -3px rgba(0, 0, 0, .3);
		border-bottom-color: transparent;
		border-right-color: transparent;
	}

	&__title {
		margin-bottom: rem(10);
	}

	.button_orange {
		display: flex;
		justify-content: center;
		margin-bottom: rem(10);
	}

	.button_border {
		display: flex;
		justify-content: center;
	}
}

.article__related {
	display: flex;
	flex-direction: column;

	hr {
		width: 100%;
		height: 1px;
		background-color: #e0e0e0;
		margin: 15px 0;	
	}

	&-between {
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin-bottom: 20px;

		@include min {
			flex-direction: column;
			align-items: baseline;

			.social-block {
				margin-bottom: 10px;
			}

		}
	}

	&-item {
		display: flex;
		flex-direction: column;

		h2 {
			margin-bottom: 20px;
		}

		.main_page__items {
			
			.item {
				width: calc((100% - 2.5rem) / 3);

				&:nth-child(3n) {
					margin-right: 0;
				}

				&:nth-child(4n) {
					margin-right: 20px;

					@include min {
						margin-right: 0;
					}
				}

				@include min {
					width: calc((100% - 1.25rem) / 2);
					margin-right: rem(20) !important;

					&:nth-child(2n) {
						margin-right: 0 !important;
					}

					&:nth-child(3n) {
						// margin-right: rem(20);
					}
				}
			}
		}
	}
}